include desktop {\n    padding: 0 32px;\n    max-width: 1264px;\n  }\n}","@import '~normalize.css';\n\n// utils\n@import '../style/utils/mixin';\n@import '../style/utils/container';\n@import '../style/utils/fonts';\n@import '../style/utils/is-hidden';\n@import '../style/utils/reset';\n@import '../style/utils/vars';\n@import '../style/common';\n\n// layout\n@import '../style/header';\n@import '../style/footer';\n\n// modals\n// @import '../style/modals/burger-modal';\n@import '../style/modals/pop-up-modal';\n\n\n\n// partials\n@import '../style/hero';\n@import '../style/companies';\n@import '../style/innovations';\n@import '../style/howWeWork';\n@import '../style/whatWeDo';\n@import '../style/offers';\n@import '../style/technologies';\n@import '../style/partnerships';\n@import '../style/blog';\n@import '../style/reviews';\n@import '../style/questions';\n@import '../style/form';\n\n\n// swiper\n@import 'swiper/css/bundle';\n\n\n","@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-Regular.ttf') ;\n    font-weight: 400;\n}\n@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-Medium.ttf') ;\n    font-weight: 500;\n}\n@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-SemiBold.ttf') ;\n    font-weight: 600;\n}\n@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-Bold.ttf') ;\n    font-weight: 700;\n}\n@font-face {\n  font-family: \"Prompt\";\n  src: url('../../fonts/Prompt-ExtraBold.ttf') ;\n    font-weight: 800;\n}\n\n  // @font-face {\n  //   font-family: 'Roboto';\n  //   src: url('../../fonts/Roboto-Regular.woff') format('woff'),\n  //     url('../../fonts/Roboto-Regular.woff2') format('woff2');\n  //   font-weight: 400;\n  // }",".is-hidden {\n    position: absolute;\n    width: 1px;\n    height: 1px;\n    margin: -1px;\n    border: 0;\n    padding: 0;\n\n    white-space: nowrap;\n    clip-path: inset(100%);\n    clip: rect(0 0 0 0);\n    overflow: hidden;\n}","*{\n    box-sizing: border-box;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np {\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\nimg {\n    display: block;\n    max-width: 100%;\n    height: auto;\n}\n\na {\n    text-decoration: none;\n    color: currentColor;\n    display: block;\n}\n\nul,ol {\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\n.link {\n    text-decoration: none;\n}\n\n.list {\n    text-decoration: none;\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\nsvg{\n    fill: currentColor;\n}\n","html,\nbody {\n  font-family: $main-font;\n  background-color: #fff;\n\n  font-size: 16px;\n  font-weight: 400;\n  line-height: 1.3;\n  color: $black;\n  \n  margin: 0 auto;\n  width: 100%;\n\n  scroll-behavior: smooth;\n}\n//   clamp(min, current, max)\n//   window 1440px / max font-size = 44px\n//   1vw = 14.4px\n//   44px / 14.4px = 3.05vw\nbody.hidden{\n  overflow: hidden;\n}\n\naddress {\n  font-style: normal;\n}\n\ninput[type='number']::-webkit-inner-spin-button,\ninput[type='number']::-webkit-outer-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n}\n\nsection{\n  padding: 80px 0;\n}\n\nh1,h2{\n  font-size: 50px;\n  line-height: 1.1;\n  font-weight: 800;\n}\n\n// btn hover focus active \n.hfa{\n  transition: $transition;\n\n  &:hover,&:focus{\n    color: $accent;\n    border: 2px solid $accent;\n    background-color: $white;\n    & svg{\n      transition: $transition;\n      color: $accent;\n    }\n  }\n  &:active{\n    color: $accent;\n    border: 2px solid $accent;\n    background-color: $white;\n    & svg{\n      transition: $transition;\n      color: $accent;\n    }\n  }\n}\n\n\n.flex{\n  display: flex;\n}\n.al-c{\n  align-items: center;\n}\n.jc{\n  justify-content: center;\n}\n.jb{\n  justify-content: space-between;\n}\n.gap-10{\n  gap: 10px;\n}\n.gap-20{\n  gap: 20px;\n}\n.gap-60{\n  gap:60px;\n}\n.gap-80{\n  gap: 80px;\n}\n\n// opacity\n.opacity-50{\n  opacity: 50%;\n}\n.opacity-60{\n  opacity: 60%;\n}\n\n\n//margin\n.mb-15{\n  margin-bottom: 15